  


html{
    
  box-sizing: border-box;
  -moz-tab-size: 4;
  -o-tab-size: 4;
     tab-size: 4;

};

body{
  margin: 0;
  height: 100vh;
  font-size: 2.6rem;
  font-family: 	system-ui,
  -apple-system, /* Firefox supports this but not yet `system-ui` */
  'Segoe UI',
  Roboto,
  Helvetica,
  Arial,
  sans-serif,
  'Apple Color Emoji',
  'Segoe UI Emoji';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root{
  height: 100%;
}
.grid-container{ /*Header*/
  display: grid;
  grid-template-areas: 
  'header'
  'main'
  'footer';

  grid-template-columns: 1fr;
  grid-template-rows: 12rem 1fr 8rem ;
  height:100%;
  
}

header{
  grid-area: header;
  background-image: linear-gradient(180deg, #ffe602, #02fff2);
  width:100%;
}

.headerH2{
  font-size: 40px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

footer{
  grid-area: footer;
  background-color: rgb(0, 0, 26);
  color: rgb(255, 253, 253);
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  text-align: center;
  font-weight: 75%;
  height: 20vh;
  width: 100%;
}

footer .buttonIcons{
        margin-right: 15px;
        letter-spacing: 2px;
        
        font-size: 15px;
        gap: 2px;  
        left: 1px;
        display: flex;
        flex-direction: row; 
        padding: 10px;
        text-align: center;
        justify-content: center;
        width: 100%;
}

footer .buttonIcons button{
  margin-right: 15px;
  letter-spacing: 2px;
  color: #1508cc;
  font-size: 30px;
  font-weight: bolder;
  display: flex;
  flex-direction: row; 
  text-align: center;
}


footer .buttonIcons button:hover{
  
  color: #fc0294;
  transform: translate(1.2);
  transition: 0.5s ease-in-out;
}

a{
  text-decoration: none;
}

a.brand:hover{
  color:rgb(233, 8, 233);
}

a.brand{
  color: rgb(20, 1, 12);
  font-size: 3rem;
  font-weight: bold;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  text-align: center;
  align-items: center;
  margin-left: 25px;
  transition: 0.6s ease-out;

}

a.brand .spanhead img{ /*for brand logo img*/
  
  max-width: 70px;
  margin-right: 8px;
}
.badge{
  background-color:darkcyan;
  color:cornsilk;
  border-radius: 75%;
  padding: 0.2rem, 0.2rem;
  margin-left: 0.2rem ;
  margin-right: 1.2rem;
  font-size: 1.2rem;
}
.row{
  display: flex ;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.row1{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  
  
}

.row.footercls{
 
  justify-content: center;
 
  
}

h1{
  font-size: 1.8rem;
  padding: 1rem 0;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  color: #f77308;
}

h2{
  font-size: 1.8rem;
  padding: 1rem 0;
  text-align: center;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: #010f08;
 
}

h3{
  font-size: 1.8rem;
  padding: 1rem 0;
  text-align: center;
  color: rgb(220, 20, 60);
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-weight: 60%;
  background-color: floralwhite;
  text-transform: uppercase;
}

h4 {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

h4:hover {
  color: #fc0294;
  transform: scaleY(1.5);
  transition: 0.3s ease-out;
  
}

h5 {
  font-size: 3rem;
  color: #15fa00;
}

/*Aside or Side bar*/
aside {
  position: fixed;
  width: 30rem;
  height: 100%;
  background-color:antiquewhite;
  z-index: 1000;
  transform: translateX(-30rem);
  transition: all 0.5s;
}

aside.open {
  transform: translateX(0rem);
  padding: 0.2rem 0.5rem;
  margin: 0 0.5rem;
  background: none;
  color: #f8f8f8;
  cursor: pointer;
}

button.open-sidebar:hover {
  border-color: antiquewhite;
}

option {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  background-color: rgb(75, 42, 1);
  color: #f3f7f5;
}
aside ul {
  padding: 0;
  list-style: none;
}

aside li {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
}

button.close-sidebar {
  padding: 0.3rem 0.8rem;
}


/*Images*/
img{
  border-radius: 0.5rem;
}

img.small{
  max-width: 5rem;
  width: 100%;
}

img.medium{
  max-width: 22rem;
  width: 100%;
  margin-top: 10px;
  margin: 10px;
  align-items: center;
  
}

img.medium:hover {
  transform: scale(1.2);
  transition: 0.5s ease-in-out;
  overflow: hidden;
}

/*card*/

.card{
  border: 0.1rem #804102 solid;
  background-color: rgb(13, 112, 107);
  border-radius: 0.9rem;
  margin: 1rem;
  text-shadow: hotpink;
  box-shadow: 4px 4px 12px #0e0000, 4px -4px 8px #ffffff;
  
}

.card-body{
  padding: 1rem;
}

.card-body > *{
  margin-bottom: 0.5rem;
}

.price{
  font-size: 1.5rem;
  color: #f3f7f5;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}


h2:hover {
  color: #15fa00;
  transform: scale(1.1);
  transition: 0.5s ease-in-out;
}

Link {
  color: #e20c9b;
}
Link:hover {
  color: #f3f7f5;
  
  
}

/*Rating*/
.rating span{  /*Rating span means all sections under the rating tag*/
  color: hsl(283, 93%, 48%);
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

}

.rating span:last-child{
  color: rgb(1, 3, 22);
  font-weight: 1.2rem;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
li {
  margin-top: 1rem;
}
input, 
select, 
textarea,
button {
  padding: 1rem;
  border-radius: 0.5rem;
  border: 0.1rem #f1e2e2 solid;
  font-size: 1.3rem;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

textarea{
  resize: none;
}


.uppercase{
  text-transform: uppercase;
}
input, 
select, 
textarea,
button:hover {
 color: #e20c9b;
 transform: scale(1.0);
 transition: 0.3s ease;
 font-weight: 2.3rem;
 
  
}

button{
  background-color: #f8f8f8;
  cursor: pointer;
}

input:hover, 
select:hover, 
textarea:hover,
button:hover {
  border: 0.1rem #404040 solid;
}
button.primary {
  background-color: #b6ff40;
}
button.block {
  width: 100%;
}

.row.top {
  align-items: flex-start;
}

.row.start {
  justify-content: flex-start;
}
.col-1 {
  flex: 1 1 25rem;
  
}
.col-2 {
  flex: 2 1 50rem;
}

.col-3 {
  flex: 32 1 75rem;
}
.min-30{
  min-width: 15rem;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  
}

.p-1 {
  padding: 1rem;
}

img.large {
  width: 65%;
}

/* Alert */
.loading{
  display: block !important;
}
.success {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: #e6ff06;
  font-weight: bolder;
}
.error {
  color: #f30606;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: bolder;
}

.alert{
  padding: 1rem;
  border: 0.1rem solid transparent;
  border: 00.5rem;
}

.alert-info{
  color:violet;
  background-color: whitesmoke;
}

.alert-danger{
  color:rgb(236, 17, 28);
  background-color: whitesmoke;
}

/*For Sign-in form*/
.form{
  max-width: 30rem;
  margin: 0 auto;
  font-size:medium;
  font-weight: 1rem;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  color:rgb(179, 0, 179);
  text-align: center;
  background: linear-gradient(270deg, #fcc11f, #f7d6d6);
  border-radius: 8px;
  
}

.form > div{
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 1rem;
  /*margin-top: 1.5rem;*/
}

.form label{
  margin: 1rem 0;
  /*display:flexbox;*/
 
}
.form-razor{
  color: #20a020;
  font-style: normal;
  font-weight: bold;
  padding: 1.2rem;
  background-color: antiquewhite;
}

.form-paypal{
  color: #20a020;
  font-style: normal;
  font-weight: bold;
  padding: 1.2rem;
  background-color: antiquewhite;
}

.dropdown{
  display: inline-block;
  position: relative;
  padding: 0.6rem;
  color: #f3f7f5;
}

.dropdown-content{
  position: relative;
  display: none;
  right: 0;
  min-width: 8rem;
  padding: 1rem;
  z-index: 1;
  background-color:rgb(253, 236, 141);
  margin: 0;
  margin-top: 0.4rem;
  border-radius: 0.5rem;  
}

.dropdown:hover .dropdown-content{
  display: block;
  position:absolute;
  color: #fc0294;
}

/* Check out steps*/
.checkout-steps > div{

  border-top: 0.9rem #4b21e4;
  border-radius: 5rem;
  color: #f0c040;
  flex: 1;
  padding: 1rem;
  font-weight: bold;
}

.checkout-steps > div.active{
  border-top-color: coral;
  color:coral;
}

/*TABLE*/
.table{
  width: 100%;
  border-collapse: collapse;

}

.table tbody tr:nth-of-type(odd) {
  /*This is means odd positions will be of same colours and the other
  rows of alternate position will be of different colours*/
  background-color:#a4a4a4 ;
}

.table thead, table tr{
  background-color: cornflowerblue;
  font-weight: bold;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.table tbody tr:nth-of-type(even) {
  /*This is means odd positions will be of same colours and the other
  rows of alternate position will be of different colours*/
  background-color:#81f7c9 ;
}
.table td, .table th {
  text-align: center;
  border: 0.1rem solid #f3f7f5;
  padding: 1rem ;
}

.table button {
  margin: 0 0.2rem;
  font-size: 1.2rem;

}
/*Carousel*/
.carousel .slide img{ /*.slide means the image would be sliding featured*/
  max-width: 20rem;
}

.menus{
  
  font-family:Verdana, Geneva, Tahoma, sans-serif;
  color: rgb(20, 1, 12);
  margin: 10px;
}

/*Search*/
.search button {
  border-radius: 0 0.5rem 0.5rem 0; /*Rounded at the topright and bottom right*/
  border-right: none;
  margin-right: 0.5rem;
  align-items: center;
}

.search input {
  border-radius: 0.5rem 0 0 0.5rem; /*Rounded at the topright and bottom right*/
  border-right: none;
  margin-left: 0.5rem;
  align-items: center;
}

.active {
  font-weight: bold;
  margin-left: 10px;
}

/* Pagination*/
.pagination a {
  padding: 1rem;
  border-radius: 0.8rem;
  border-bottom: 0.5rem;
  border: 0.2rem #615c5c solid;
  font-size: 0.9rem;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 5px;
}

.pagination a:hover {
  background-color: #fc0294;
  transform: scale(1.2);
  transition: 0.5s ease-in-out;
  
}

